<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process270"></a>

<p><br></p>
<p><span class="rvts17">大惊小怪的过往（</span><span class="rvts50">What a Fuss to Have Wrought</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:433,Y:290</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：桑谷尼亚</span><span class="rvts20"></span><span class="rvts21">特尔（</span><span class="rvts20">Sanguinia Tell</span><span class="rvts21">）</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">与桑谷尼亚谈话，选择贷一笔钱，并与他签订献血协议</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1489.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">老太太桑谷尼亚就在学校和矮人新郎官的中间。与她交谈，她很乐意为你贷款。但实际上，桑谷尼亚是一个恶魔，她会从你身上抽取鲜血作为盟约，一旦你违约，她会瞬间将你灭掉。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1490.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">最高可贷</span><span class="rvts20">10000</span><span class="rvts21">，但是需要较高的说服值。之后，你可以在她房间后面的地窖中找到她和她的几个收贷跟班儿，并且你的欠条也藏到了那里。你可以尝试偷走欠条免去债务。</span>
<br><span class="rvts21">但是，如果你贷款后，不理会她，她会在一段时间后突然出现找你还钱。</span>
<br><span class="rvts39"></span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1491.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">尝试从老太太桑谷尼亚家的室外地下室进入，会发现这是她储存</span><span class="rvts20"></span><span class="rvts21">债条</span><span class="rvts20"></span><span class="rvts21">的地方。偷走债条，可以免除身上的债务。如果失败，将会迎来一场战斗。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



